import Config from './config.js'
import uni_request from '@/utils/uni_request/uni_request.js'
let baseURL = Config.baseUrl
console.log(baseURL);
const request = uni_request({
	baseURL: baseURL,
	timeout: 10000, //  60 
	statusCode: [200, 401]
})

request.interceptors.request.use(async (config, ...args) => { // 
	// ...
	// if (!$store.state.app.token) { //  token
	// 	// await new Promise(resolve => ))
	// }
	// config.header.Authorization = 'Bearer ' + $store.state.app.token //  token 
	// console.log(config);
	let token =  uni.getStorageSync('token')
	config.body.token = token
	// config.header.token = token
	config.header.ContentType = 'application/json'
	if (!config.header.hideLoading) {
		uni.showLoading({
			title: ''
		})
	}
	return config
})

request.interceptors.response.use(async (response, ...args) => { // 
	// ...
	uni.hideLoading();
	return response
})

request.onerror = (...args) => console.log(args) // 

export default request
