export function getTimestamp(str) {
	let date = new date(str);
	return date.getTime();
}

export function countDownTime(str, type) {
	var date1 = new Date(str); //
	var date2 = new Date(); //
	var date3 = date1.getTime() - date2.getTime(); //
	if (date3 <= 0) {
		return 0;
	}
	//
	var days = Math.floor(date3 / (24 * 3600 * 1000));

	//
	var leave1 = date3 % (24 * 3600 * 1000); //
	var hours = Math.floor(leave1 / (3600 * 1000));

	//
	var leave2 = leave1 % (3600 * 1000); //
	var minutes = Math.floor(leave2 / (60 * 1000));

	//
	var leave3 = leave2 % (60 * 1000); //
	var seconds = Math.round(leave3 / 1000);
	let countDownStr = days + "" + hours + "" + minutes + "" + seconds + "";
	switch (type) {
		case 'day':
			countDownStr = days + "";
			break;
		case 'minutes':
			countDownStr = days + "" + hours + "" + minutes + "";
			break;
		case 'hours':
			countDownStr = days + "" + hours + "";
			break;
		default:
			break;
	}
	return countDownStr;
}

export function dateFormat(fmt, str) {
	let date = new Date(str);
	let ret;
	const opt = {
		"Y+": date.getFullYear().toString(), // 
		"m+": (date.getMonth() + 1).toString(), // 
		"d+": date.getDate().toString(), // 
		"H+": date.getHours().toString(), // 
		"M+": date.getMinutes().toString(), // 
		"S+": date.getSeconds().toString() // 
		// 
	};
	for (let k in opt) {
		ret = new RegExp("(" + k + ")").exec(fmt);
		if (ret) {
			fmt = fmt.replace(ret[1], (ret[1].length == 1) ? (opt[k]) : (opt[k].padStart(ret[1].length, "0")))
		};
	};
	return fmt;
}
